.about {
  width: 50%;
  margin: 0 auto;
  padding-top: 3em;
  text-align: left;
}

.about-paper {
  padding: 1em;
  margin-bottom: 2em;
}

.date-paper {
  padding: 1em;
  margin-bottom: 2em;
}

.date-paper span {
  vertical-align: bottom;
}

.date-picker {
  display: inline-block;
  margin-left: 0.5em;
}

.best-sellers .author {
  margin-top: -0.4em;
}

.best-sellers .description {
  margin-top: 0.6em;
}

.loading {
  margin: 1em 0 0 1em;
  color: #999;
}
.best-sellers {
  width: 80%;
  margin: 0 auto;
  padding-top: 6em;
  text-align: left;
}

header .menu-button {
  margin: 1em;
  color: #FFF;
}

body {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  text-align: center;
}

